/* Global Styles */
:root {
  --primary-color: #31326f;
  --btn-primary-color: #9ddfd3;
  --dark-color: #ffc93c;
  --card-color: #ffc93c;
  --card-text-color: #fff;
  --card-text-highlight: #31326f;
  --light-color: #dbf6e9;
  --danger-color: #31326f;
  --delete-color: #f54748;
  --success-color: #050505;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Raleway', sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

/* Utilities */
.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 1rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

/* Text Styles*/
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--primary-color);
}

.text-dark {
  color: var(--dark-color);
}

/* Padding */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* Margin */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
  border-radius:20px;
}

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
}

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--btn-primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.bg-card{
  background: var(--card-color);
  color:var(--card-text-color);
  border-radius:20px;
}
.card-highlight{
  color: var(--card-text-highlight);
}
.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}
 .border-rad{
  border-radius:20px;
}
.btn-like{
  background: #fbdc6b;
  color:white;
}
.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}
.btn-delete{
  background: var(--delete-color);
  float:right;
  color:white;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
  border-radius:20px;
}

.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #ccc solid 1px;
  border-radius:20px;
}
.bg-comment{
  background:#fbdc6b;
  border-radius:20px;
}
.comment-text{
  box-shadow:0px 0px 5px 0px rgba(0,0,0,0.2);  
  border-radius:20px;
  padding:10px;
  background:#ffc93c;
}

.round-img {
  border-radius: 50%;
}

.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0;
}

/* Overlay */
.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Forms */
.form .form-group {
  margin: 1.2rem 0;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 1.2rem;
  border: 1px solid #ccc;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form .social-input {
  display: flex;
}

.form .social-input i {
  padding: 0.5rem;
  width: 4rem;
}

.form .social-input i.fa-twitter {
  color: #38a1f3;
}
.form .social-input i.fa-facebook {
  color: #3b5998;
}
.form .social-input i.fa-instagram {
  color: #3f729b;
}
.form .social-input i.fa-youtube {
  color: #c4302b;
}
.form .social-input i.fa-linkedin {
  color: #0077b5;
}

.table th,
.table td {
  padding: 1rem;
  text-align: left;
  background:var(--light-color);
}

.table th {
  background: var(--card-color);
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: absolute;
  z-index: 1;
  width: 100%;
  top: 0;
  opacity: 0.9;
  box-shadow: 0px 0px 2px 2px rgba(0,0,0,.2);
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: var(--primary-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Landing Page */
.landing {
  position: relative;
  background: url('./img/showcase.jpg') no-repeat center center/cover;
  height: 100vh;
}

.landing-inner {
  color: #fff;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Profiles Page */
.profiles{
  display: grid;
  grid-template-columns: 2fr 2fr;
  grid-gap: 10px;
}
.profile {
  display: grid;
  grid-template-columns: 3fr 6fr 5fr;
  align-items: flex-start;
  grid-gap: 1rem;
  padding: 0.5rem;
  overflow: hidden;
  height: auto;
  line-height: 1.5;
  border-radius: 20px;
  margin-bottom: 1rem;
  border-radius: 20px;
  text-transform: capitalize;
}

/* Profile Page */
.profile-grid {
  display: grid;
  grid-template-areas:
    'profile about'
      'exp edu'
    'github github';
  grid-gap: 1rem;
}

.profile-top {
  grid-area: profile;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius:20px;
  text-transform: capitalize;
}

.profile ul{
  background: #fbdc6b;
  height: 100%;
  padding: 10px;
  border-radius: 20px;
  display: grid;
  box-shadow: 0px 0px 2px 2px rgb(134 117 0 / 11%);
}
.profile ul li{
  height: 26px;
    overflow: hidden;
    text-align:left;
}
.profile-card-sec{
  height: 100%;
  background: #fbdc6b;
  border-radius: 20px;
  box-shadow: 0px 0px 2px 2px rgb(134 117 0 / 11%);
  padding: 10px;
  text-align: left;
  color: #31326f;
}
.profile-top img {
  width: 100px;
  border-radius: 50%;
}

.profile-top .icons a {
  color: #fff;
  margin: 0 0.3rem;
}

.profile-top .icons a:hover {
  color: var(--dark-color);
}

.profile-about {
  grid-area: about;
  text-align: center;
  text-transform: capitalize;
}

.profile-about .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.profile-exp {
  grid-area: exp;
  text-transform: capitalize;
}
.profile-edu{
  text-transform: capitalize;
}

.profile-edu {
  grid-area: edu;
}

.profile-exp h2,
.profile-edu h2 {
  margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dotted;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
  border: 0;
}

.profile-exp p,
.profile-edu p {
  margin: 0.5rem 0;
}

.profile-github {
  grid-area: github;
}

.profile-github .repo {
  display: flex;
}

.profile-github .repo > div:first-child {
  flex: 7;
  flex-basis: 70%;
}

.profile-github > div:last-child {
  flex: 3;
  flex-basis: 20%;
}

/* Posts Page */
.post-form .post-form-header {
  background: var(--primary-color);
  padding: 0.5rem;
}

.post {
  display: grid;
    grid-template-columns: 1fr 8fr;
    grid-gap: 1rem;
    align-items: start;
    box-shadow: 0px 0px 2px 2px rgb(134 117 0 / 11%);
}

.post > div:first-child {
  text-align: center;
}

.post img {
  width: 50px;
}
.post-heading{
  font-size: 20px;
  padding: 10px;
  background: #fbdc6b;
  border-radius: 20px;
  font-weight: bold;
  color: var(--primary-color);
  text-transform: capitalize;
  box-shadow: 0px 0px 9px 0px rgb(0 0 0 / 13%);
}
.post .comment-count {
  background: var(--light-color);
  color: var(--primary-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 7rem;
  }

  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  /* Profiles Page */
  .profiles{
    grid-template-columns: 1fr;
  }
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    height:auto;
    
  }


  .profile ul {
    display: none;
  }

  /* Profile Page */

  .profile-top img,
  .profile img {
    width: 100px;
    margin: auto;
    border-radius:50%;
  
  }

  .profile-grid {
    grid-template-areas:
      'profile'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
  }

  .post {
    grid-template-columns: 1fr;
    grid-gap:5px;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}
